const BASE_URL = "http://localhost:9921";

export const fetchTodaysSchedules = async () => {
  try {
    const response = await fetch(`${BASE_URL}/timetable/today`);
    if (!response.ok) {
      throw new Error("Failed to fetch today's schedules");
    }
    const data = await response.json();
    return data;
  } catch (error) {
    console.error(error);
    return [];
  }
};

