import React, { useEffect, useState } from 'react';
import StatCard from "../StatCard/statcard";
import { fetchTodaysSchedules } from "../../utils/Services/dashboardService";


const DashboardPage = () => {
  const [schedules, setSchedules] = useState([]);

  useEffect(() => {
    const getSchedules = async () => {
      const data = await fetchTodaysSchedules();
      setSchedules(data);
    };
    getSchedules();
  }, []);

  return (
    <div className="grid grid-cols-1 gap-6 p-4 sm:grid-cols-2 lg:grid-cols-4">
      <StatCard title="Total Classes" value="124" color="bg-blue-500" />
      <StatCard title="Active Lecturers" value="28" color="bg-green-500" />
      <StatCard title="Available Rooms" value="15" color="bg-yellow-500" />
      <StatCard title="Weekly Sessions" value="320" color="bg-purple-500" />

      <div className="col-span-1 sm:col-span-2 lg:col-span-2 bg-white rounded-lg shadow-md p-6">
        <h3 className="text-lg font-semibold mb-4">Today's Schedules</h3>
        <div className="space-y-3 max-h-64 overflow-y-auto">
          {schedules.length > 0 ? (
            schedules.map((schedule, index) => (
              <div key={index} className="flex justify-between items-center p-3 bg-gray-50 rounded-md">
                <div>
                  <p className="font-medium text-gray-800">{schedule.courseName}</p>
                  <p className="text-sm text-gray-600">
                    {schedule.lecturerName} â€¢ {schedule.roomName}
                  </p>
                </div>
                <div className="text-right">
                  <p className="text-sm font-medium text-gray-800">{schedule.dayName}</p>
                  <p className="text-sm text-gray-600">
                    {schedule.startTime} - {schedule.endTime}
                  </p>
                </div>
              </div>
            ))
          ) : (
            <p className="text-gray-500">No schedules available for today.</p>
          )}
        </div>
      </div>

      <div className="col-span-1 sm:col-span-2 lg:col-span-2 bg-white rounded-lg shadow-md p-6">
        <h3 className="text-lg font-semibold text-gray-600 mb-4">Room Utilization</h3>
        <div className="h-64 flex items-center justify-center bg-gray-100 rounded-md">
          <p className="text-gray-500">Room usage chart would go here</p>
        </div>
      </div>
    </div>
  );
};

export default DashboardPage;